.not-found img {
	max-width: 140px;
}
.not-found {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	background-image: linear-gradient(#a256ca, #efefef);
	height: 100%;
}
.not-found h2 {
	color: #64008a;
	font-weight: bold;
	padding: 10px 0px;
}
.not-found p {
	color: #000;
	font-size: 16px;
}
