.header-container {
	height: 100vh;
	display: flex;
	background-image: linear-gradient(#a256ca, #efefef);
}
.header-content {
	display: flex;
	width: 100%;
	max-width: 1000px;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	margin: 0 auto;
}
.header-right {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.header-left img {
	max-width: 100%;
}
.title {
	color: #64008a;
	font-weight: 200;
	font-size: 50pt;
}

.header-right img {
	max-width: 200px;
	max-height: 200px;
	-webkit-filter: drop-shadow(5px 5px 5px #aaa);
	filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, .3));
}

@media only screen and (max-width: 600px) {
	.header-content {
		flex-wrap: wrap;
	}
	.header-right img {
		max-width: 100px;
		max-height: 100px;
	}
	.header-left {
		max-width: 80%;
		justify-content: center;
		align-items: center;
	}
	.title {
		font-size: 30pt;
	}
}
