.video-container {
	padding: 72px 0px;
	max-width: 1000px;
	margin: 0px auto;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.video-content {
	background: #ffffff;
	width: 500px;
	height: 100%;
	padding: 20px;
	box-shadow: 5px 5px 10px #848484;
	border-top-left-radius: 25px;
	border-bottom-right-radius: 25px;
	margin: 0px 10px;
}
.video-content iframe {
	position: relative;
	top: 10;
	left: 10;
	width: 100%;
}
.video-description {
	max-width: 450px;
}
.video-description p {
	font-size: 15pt;
	text-align: center;
}

@media only screen and (max-width: 800px) {
	.video-container {
		flex-wrap: wrap;
	}
	.video-content {
		margin: 0px auto;
	}
	.video-description {
		width: 90%;
		margin: 0px auto;
		padding-bottom: 30px;
	}
}
