.container {
	display: flex;
	justify-content: center;
	height: 100%;
}
.form {
	text-align: center;
	padding: 20px;
	max-width: 900px;
	margin: 0px auto;
}
.form-header {
	color: #64008a;
	padding-bottom: 20px;
}
.form-header img {
	max-width: 100%;
	width: 120px;
}
.form-group {
	padding: 10px 0px;
}
.form-group p {
	padding: 10px 0px;
	font-size: 18px;
}
.form-group textarea {
	box-shadow: 1px 1px 10px #aaa;
	padding: 20px;
	width: 100%;
	border-top-left-radius: 25px;
	border-bottom-right-radius: 25px;
	font-size: 15px;
	border: none;
}
.form-group label {
	font-size: 18px;
	margin: 15px;
}
.form-group input[type="radio"] {
	color: #64008a;
	margin: 15px;
}
.form-footer {
	padding: 20px 0px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.form-footer a {
	color: #fff;
	background: #64008a;
	width: 100%;
	padding: 20px;
	font-size: 20px;
	font-weight: bold;
	text-decoration: none;
	border-top-left-radius: 25px;
	border-bottom-right-radius: 25px;
}
.form-footer a:hover {
	background: #a135db;
}
.loading {
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.loading-container {
	color: #64008a;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}
.loading img {
	max-width: 100%;
}

.alert {
	display: flex;
	align-items: center;
	justify-content: space-between;
	color: #fff;
	font-size: 15px;
	padding: 15px 10px;
	border-radius: 10px;
}
.alert-error {
	background: #c10d0d;
}
.alert-sucess {
	background: #169618;
}
.alert button {
	background: transparent;
	border: none;
	color: #ddd;
	font-size: 15px;
	font-weight: bold;
	cursor: pointer;
}
