.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.6);
}

.modal-main {
	position: fixed;
	background: #fff;
	width: 60%;
	height: auto;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	padding: 20px;
	border-top-left-radius: 25px;
	border-bottom-right-radius: 25px;
	border: none;
}

.modal-main > button {
	width: 100%;
	background: #64008a;
	padding: 10px 0px;
	border: none;
	border-bottom-right-radius: 25px;
	border-top-left-radius: 25px;
