.block-container {
	display: flex;
	padding: 72px 20px;
}
.block-content {
	display: flex;
	width: 100%;
	max-width: 1000px;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	margin: 0 auto;
}

.left-block {
	min-width: 25%;
	max-width: 150%;
}
.left-block img {
	max-width: 100%;
}

@media only screen and (max-width: 800px) {
	.block-content {
		flex-wrap: wrap;
	}
	.left-block {
		width: 50%;
	}
}

.right-block {
	padding: 0px 20px;
}
.right-block h2 {
	text-align: center;
	color: #64008a;
	font-weight: 200;
	font-size: 40pt;
	padding-bottom: 15px;
}
.right-block p {
	font-size: 15pt;
	text-align: center;
}
