.news {
  display: flex;
  padding: 72px 0px;
  flex-direction: column;
}
.news h1 {
  text-align: center;
  padding-bottom: 40px;
  color: #64008a;
  font-weight: 200;
  font-size: 30pt;
}
.news-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1000px;
  margin: 0px auto;
}
.new-item {
  display: flex;
  width: 100%;
  max-width: 45%;
  flex-direction: row;
  margin: 10px;
}
.new-item a {
  box-shadow: 0px 2px 10px #848484;
  padding: 20px;
  width: 100%;
  border-top-left-radius: 25px;
  border-bottom-right-radius: 25px;
  text-decoration: none;
}
.new-item a h2 {
  color: rgba(0, 0, 0, 0.6);
  padding-bottom: 10px;
}
.new-item a strong {
  color: rgba(0, 0, 0, 0.4);
  padding: 10px 0px;
}
.bottom {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.bottom > img {
  max-width: 100px;
  max-height: 50px;
}

@media only screen and (max-width: 600px) {
  .block-content {
    flex-wrap: wrap;
  }
  .left-block {
    width: 50%;
  }
  .new-item {
    max-width: 100%;
  }
}
